*{
  margin:0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#board{
  display: -ms-flexbox;
  display: flex;
  width:300px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.square{
  width:100px;
  height:100px;
  border:1px solid #000000;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align:center;
      align-items:center;
  font-size:30px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
